html, body {
  width: 100%;
  height: 100%;
}

body {
  font-family: 'Avenir','Helvetica Neue','Helvetica','Arial','Hiragino Sans','ヒラギノ角ゴシック','YuGothic','Yu Gothic','メイリオ','Meiryo','ＭＳ Ｐゴシック','MS PGothic' !important;
}

.btn-xl {
  padding: 1.25rem 2.5rem;
}

.content-section {
  padding-top: 7.5rem;
  padding-bottom: 7.5rem;
}

.content-section-heading h2 {
  font-size: 3rem;
}

.content-section-heading h3 {
  font-size: 1rem;
  text-transform: uppercase;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
}

/* koozaki */
.scroll-to-top {
  position: fixed;
  right: 15px;
  bottom: 15px;
  width: 50px;
  height: 50px;
  text-align: center;
  line-height: 45px;
  z-index: 1000;
  -webkit-transition: opacity 100ms ease-out;
  transition: opacity 100ms ease-out;
}

.scroll-to-top i {
  font-weight: 800;
}

.masthead {
  display: table;
  position: relative;
  width: 100%;
  height: auto;
  min-height: 30rem;
  padding-top: 8rem;
  padding-bottom: 8rem;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.masthead h1 {
  font-size: 2.5rem;
  margin: 0;
  padding: 0;
}

@media (min-width: 992px) {
  .masthead {
    height: 100vh;
  }
  .masthead h1 {
    font-size: 5.5rem;
  }
}

/* Side Menu */
#sidebar-wrapper {
  position: fixed;
  z-index: 2;
  right: 0;
  width: 250px;
  height: 100%;
  border-left: 1px solid #ecb807;
  -webkit-transition: all 200ms ease-out 0ms;
  transition: all 200ms ease-out 0ms;
  -webkit-transform: translateX(250px);
  transform: translateX(250px);
}

.sidebar-nav {
  position: absolute;
  top: 0;
  width: 250px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.sidebar-nav li.sidebar-nav-item a {
  display: block;
  text-decoration: none;
  padding: 15px;
}

.sidebar-nav li a:hover,
.sidebar-nav li a:active,
.sidebar-nav li a:focus {
  text-decoration: none;
}

@media (min-width: 992px) {
  .sidebar-nav li a:hover {
    opacity: 0.5;
  }
}

.sidebar-nav > .sidebar-brand {
  font-size: 1.2rem;
  height: 80px;
  line-height: 50px;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 15px;
}

.sidebar-nav > .sidebar-brand a:hover {
  background: none;
}

#sidebar-wrapper.active {
  right: 250px;
  width: 250px;
  -webkit-transition: all 200ms ease-out 0s;
  transition: all 200ms ease-out 0s;
}

.menu-toggle {
  position: fixed;
  right: 15px;
  top: 15px;
  width: 50px;
  height: 50px;
  text-align: center;
  line-height: 50px;
  z-index: 999;
}

@keyframes serviceIconAnime {
  0% {
    transform: scale(1.0);
  }
  100% {
    transform: scale(1.1);
  }
}

.service-icon {
  display: block;
  width: 7rem;
  height: 7rem;
  line-height: 7.5rem;
  font-size: 2.25rem;
  -webkit-box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.1);
  animation: serviceIconAnime 750ms ease-in-out infinite alternate;
}

.callout {
  padding: 15rem 0;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.callout h2 {
  font-size: 3.5rem;
  font-weight: 700;
  display: block;
  max-width: 30rem;
}

.portfolio-item {
  display: block;
  position: relative;
  overflow: hidden;
  max-width: 530px;
  margin: auto auto 1rem;
}

.portfolio-item .caption {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  z-index: 1;
}

.portfolio-item .caption .caption-content {
  margin: auto 2rem 2rem;
}

.portfolio-item .caption .caption-content h2 {
  font-size: 0.8rem;
  text-transform: uppercase;
}

.portfolio-item .caption .caption-content p {
  font-weight: 300;
  font-size: 1.2rem;
}

@media (min-width: 992px) {
  .portfolio-item {
    max-width: none;
    margin: 0;
  }
  .portfolio-item .caption {
    -webkit-transition: -webkit-clip-path 100ms ease-out;
    transition: clip-path 100ms ease-out;
    -webkit-clip-path: inset(0px);
    clip-path: inset(0px);
  }
  .portfolio-item .caption .caption-content {
    -webkit-transition: opacity 100ms ease-out;
    transition: opacity 100ms ease-out;
    margin-left: 5rem;
    margin-right: 5rem;
    margin-bottom: 5rem;
  }
  .portfolio-item img {
    -webkit-transition: -webkit-clip-path 100ms ease-out;
    transition: clip-path 100ms ease-out;
    -webkit-clip-path: inset(-1px);
    clip-path: inset(-1px);
  }
  .portfolio-item:hover img {
    -webkit-clip-path: inset(2rem);
    clip-path: inset(2rem);
  }
  .portfolio-item:hover .caption {
    -webkit-clip-path: inset(2rem);
    clip-path: inset(2rem);
  }
}

footer.footer {
  padding-top: 5rem;
  padding-bottom: 5rem;
}

footer.footer .social-link {
  display: block;
  width: 4rem;
  height: 4rem;
  line-height: 4.3rem;
  font-size: 1.5rem;
  -webkit-transition: transform 100ms ease-out;
  transition: transform 100ms ease-out;
  -webkit-box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.1);
}

footer.footer .social-link {
  text-decoration: none;
}

@media (min-width: 992px) {
  footer.footer .social-link:hover {
    transform-origin: center center;
    transform: scale(1.2);
  }
}

a {
  color: #1D809F;
}

.btn-primary {
  background-color: #1D809F !important;
  border-color: #1D809F !important;
  color: #fff !important;
}

.btn-primary:hover, .btn-primary:focus, .btn-primary:active {
  background-color: #155d74 !important;
  border-color: #155d74 !important;
}

.btn-secondary {
  background-color: #ecb807 !important;
  border-color: #ecb807 !important;
  color: #fff !important;
}

.btn-secondary:hover, .btn-secondary:focus, .btn-secondary:active {
  background-color: #ba9106 !important;
  border-color: #ba9106 !important;
}

.btn-dark {
  color: #fff !important;
}

.btn {
  -webkit-box-shadow: 0px 3px 3px 0px rgba(0, 0, 0, 0.1);
  box-shadow: 0px 3px 3px 0px rgba(0, 0, 0, 0.1);
  font-weight: 700;
}

.bg-primary {
  background-color: #1D809F !important;
}

.text-primary {
  color: #1D809F !important;
}

.text-secondary {
  color: #ecb807 !important;
}

.text-faded {
  color: rgba(255, 255, 255, 0.7) !important;
}

/**
 * Original CSS
 */
.bg-dark, a.bg-dark:focus, a.bg-dark:hover, button.bg-dark:focus, button.bg-dark:hover {
    background-color: #000 !important;
}

.bg-light, a.bg-light:focus, a.bg-light:hover, button.bg-light:focus, button.bg-light:hover {
  background-color: #fff !important;
}

.text-white, a.text-white:focus, a.text-white:hover, button.text-white:focus, button.text-white:hover {
  color: #fff !important;
}

.header-logo img {
  width: 50vw;
}

@media (min-width: 992px) {
  .header-logo img {
    -webkit-transition: transform 100ms ease-out;
    transition: transform 100ms ease-out;
  }

  .header-logo img:hover {
    transform: scale(1.2);
  }
}

.portfolio-item .caption .caption-content h2 {
  transform: translateY(30px);
}
